.teacher_info {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 3px 7px rgba(0,0,0,0.2);
    border: 1px solid white;
    border-radius: 20px;
    transition: transform 0.5s;
    padding: 0 15px;
}

img {
    width: 300px;
    height: 400px;
    border-radius: 20px
}

.teacher_info h2 {
    text-align: center;
}

@media (min-width: 1024px) {
    .teacher_info:hover {
        transform: scale(1.1);
    }

    .teacher_info p {
        font-size: 12px;
        text-align: center;
        color: black;
    }
}