.lesson_info {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 3px 7px rgba(0,0,0,0.2);
    border: 1px solid white;
    border-radius: 20px;
    transition: transform 0.5s;
    padding: 0 15px;
}

.lesson_info h2 {
    text-align: center;
}

@media (min-width: 1024px) {
    .lesson_info p {
        font-size: 12px;
        text-align: center;
    }
}